@import url("https://fonts.googleapis.com/css2?family=Roboto&display=swap");

* {
  margin: 0%;
  padding: 0%;
  box-sizing: border-box;
}

.App{
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  background-color: #f0f2f5;
}

body {
  font-family: "Roboto";
}

a {
  text-decoration: none;
  color: #1a73e8;
  display: block;
  font-size: 14px;
}

.redirect-btn {
  background-color: #1a73e8;
  color: white;
  border: none;
  height: 38px;
  padding: 0 25px;
  border-radius: 5px;
  cursor: pointer;
}

.redirect-btn:hover {
  background-color: #1864c9;
}

.container {
  max-width: 450px;
  border: 1px solid rgb(228, 228, 228);
  margin: auto;
  margin-top: 4rem;
  border-radius: 10px;
  padding: 2rem;
  height: 500px;
}

.top-content {
  text-align: center;
}

img {
  width: 80px;
  margin: 10px 0;
}

h2 {
  font-size: 20px;
  font-weight: 100;
  margin-bottom: 10px;
}

.heading {
  margin-bottom: 10px;
}

.email-box {
  background-color: #f0f0f0;
  border-radius: 15px;
  padding: 5px 10px;
  font-size: 14px;
  color: #333;
  width: fit-content;
  display: flex;
  align-items: center;
 margin: 0 auto 30px auto;
}

input{
  display: block;
  border: 1px solid rgb(228, 228, 228);
  font-size: 16px;
  width: 100%;
  height: 55px;
  padding: 0 15px;
  margin-bottom: 10px;
  position: relative;
  z-index: 2;
  background-color: transparent;
  outline: none;
  border-radius: 5px;
}

.inputs {
  position: relative;
}

.input-label {
  position: absolute;
  top: 15px;
  font-size: 1.1rem;
  left: 14px;
  color: rgb(122, 122, 122);
  font-weight: 100;
  transition: 0.1s ease;
  background-color: white;
  padding: 0 5px;
}

input:focus ~ .input-label {
  top: -7px;
  color: #1864c9;
  font-size: 13px;
  background-color: rgb(255, 255, 255);
  z-index: 2;
}

input:target ~ .input-label {
  top: -7px;
  color: #1864c9;
  font-size: 13px;
  background-color: rgb(255, 255, 255);
  z-index: 2;
}

.input:focus {
  border: 2px solid #1a73e8;
}

.link-btn {
  margin-bottom: 2rem;
}

.color {
  color: rgb(90, 90, 90);
  font-size: 14px;
  margin-bottom: 5px;
}

.btn-group {
  display: flex;
  justify-content: flex-end;
}

.create-btn {
  border: none;
  background-color: transparent;
  color: #1a73e8;
  font-weight: bold;
  cursor: pointer;
  height: 35px;
  padding: 10px 5px;
}

.next-btn {
  background-color: #1a73e8;
  color: white;
  border: none;
  height: 38px;
  padding: 0 25px;
  border-radius: 5px;
  cursor: pointer;
}

.create-btn:hover {
  background-color: #e8f2ff6e;
}

.next-btn:hover {
  background-color: #1864c9;
}

.cont-div{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 20px auto;
}

.cont-div-1{
  display: flex;
  margin: 30px auto;
}

.cont-div-1 h2{
  font-size: 50px;
  font-weight: 400;
  color: #1864c9;
  margin-bottom: 10px;
}